import { ChangeEvent, HTMLInputTypeAttribute, memo, useCallback, useEffect, useState } from 'react';
import { Controller, useFormContext } from 'react-hook-form';
import { NumberFormatBase } from 'react-number-format';

import _get from 'lodash/get';
import {
  formatToKoreanAmount,
  currencyFormatter,
  dateFormatter,
  telFormatter
} from 'utils/formatters';

import ErrorMessage from 'components/forms/ErrorMessage';
import Label from 'components/forms/Label';
import Icon from 'components/ui/Icon';
import Typography from 'components/ui/Typography';

import classNames from 'classnames';
import './index.scss';

// ----------------------------------------------------------------------

export type InputProps = {
  name: string;
  placeholder: string;
  type?: HTMLInputTypeAttribute | 'today-date' | 'phone-number' | 'currency';
  disabled?: boolean;
  label?: string;
  value?: string | number;
  defaultValue?: string | number;
  labelWidth?: string;
  required?: boolean;
  flexDirection?: 'row' | 'column';
  variant?: 'default' | 'bordered' | 'ghost';
  size?: 'lg' | 'md' | 'sm' | 'xs';
  align?: 'left' | 'center' | 'right';
  color?: 'default' | 'primary' | 'tertiary';
  unit?: string;
  outside?: boolean;
  iconName?: IconTypes;
  clearable?: boolean;
  onClick?: () => void;
  onChange?: (
    e: ChangeEvent<HTMLInputElement> | { target: { name: string; value: string | number } }
  ) => void;
  autoComplete?: 'on' | 'off';
  hasContext?: boolean;
  showCurrencyValue?: boolean;
};

const COLOR_MAPS = {
  default: '',
  primary: 'text-field--primary',
  tertiary: 'text-field--tertiary'
};

const VARIANT_MAPS = {
  default: '',
  bordered: 'input-bordered text-field--bordered',
  ghost: 'input-ghost text-field--ghost'
};

const SIZE_MAPS = {
  lg: 'text-field--lg',
  md: 'text-field--md',
  sm: 'text-field--sm',
  xs: 'text-field--xs'
};

const ALIGN_MAPS = {
  left: 'text-field--left',
  center: 'text-field--center',
  right: 'text-field--right'
};

export const ContextInput = ({
  name,
  placeholder,
  type = 'text',
  disabled = false,
  label,
  required = false,
  defaultValue,
  flexDirection = 'column',
  variant = 'bordered',
  size = 'md',
  align = 'left',
  color = 'default',
  unit,
  outside = false,
  iconName,
  clearable = false,
  onClick,
  onChange,
  autoComplete = 'off',
  showCurrencyValue = false,
  ...props
}: InputProps) => {
  const { control, reset, formState, watch, setValue, getValues } = useFormContext();

  const errorMessage = _get(formState?.errors, `${name}.message`, '') as string;

  const format = useCallback((type: InputProps['type']) => {
    switch (type) {
      case 'today-date':
        return dateFormatter;
      case 'phone-number':
        return telFormatter;
      case 'currency':
        return currencyFormatter;
      default:
        break;
    }
  }, []);

  const onInputChange = useCallback(
    (value: string) => {
      switch (type) {
        case 'currency': {
          const transformedValue = Number(value.replace(/,/g, ''));

          return setValue(name, transformedValue);
        }
        case 'today-date': {
          const transformedValue = Number(value.replace(/-/g, ''));

          return setValue(name, transformedValue);
        }
        case 'phone-number': {
          const transformedValue = value.replace(/-/g, '');

          return setValue(name, transformedValue);
        }
        default: {
          return setValue(name, value);
        }
      }
    },
    [type, name, setValue]
  );

  useEffect(() => {
    if (!onChange) return;
    watch((_value, { name }) => {
      onChange({ target: { name: name || '', value: getValues(name || '') } });
    });
  }, [getValues, name, onChange, watch]);

  return (
    <Controller
      name={name}
      control={control}
      defaultValue={defaultValue}
      render={({ field: { ref, ...fieldRest } }) => {
        return (
          <div
            className={classNames(
              'text-field',
              {
                'text-field--row': flexDirection === 'row'
              },
              {
                'text-field--disabled': disabled
              },
              VARIANT_MAPS[variant],
              SIZE_MAPS[size],
              ALIGN_MAPS[align],
              COLOR_MAPS[color]
            )}
          >
            {label && <Label text={label} required={required} color={color} />}
            <div className="text-field__outside">
              <div className="text-field__actions">
                {iconName && (
                  <button type="button" className="text-field__icon-pressable" onClick={onClick}>
                    <Icon name={iconName} className="text-field__icon" />
                  </button>
                )}
                {type === 'currency' || type === 'today-date' || type === 'phone-number' ? (
                  <NumberFormatBase
                    className="text-field__form"
                    getInputRef={ref}
                    placeholder={placeholder}
                    disabled={disabled}
                    autoComplete={autoComplete}
                    {...fieldRest}
                    format={format(type)}
                    onValueChange={(values) => {
                      onInputChange(values.value);
                    }}
                    onChange={onChange}
                  />
                ) : (
                  <input
                    type={type}
                    className="text-field__form"
                    ref={ref}
                    placeholder={placeholder}
                    disabled={disabled}
                    autoComplete={autoComplete}
                    {...fieldRest}
                    value={fieldRest.value || ''}
                  />
                )}
                {!outside && unit && (
                  <Typography size="xs" className="text-field__unit">
                    {unit}
                  </Typography>
                )}
                {clearable && !disabled && fieldRest.value !== '' && (
                  <button
                    type="button"
                    className="text-field__clearable"
                    onClick={() => {
                      reset({
                        [name]: ''
                      });
                    }}
                  >
                    <Icon
                      name="icon-basic-close"
                      size="text-[1.25rem]"
                      color="text-gray-600 hover:text-gray-700"
                    />
                  </button>
                )}
              </div>
              {!!outside && unit && (
                <Typography size="xs" className="text-field__outside-unit">
                  {unit}
                </Typography>
              )}
            </div>
            {errorMessage && <ErrorMessage text={errorMessage} />}
            {!errorMessage && showCurrencyValue && type === 'currency' && (
              <Typography size="sm" className="text-field__currency-format">
                {formatToKoreanAmount(getValues(name) as string)}
              </Typography>
            )}
          </div>
        );
      }}
      {...props}
    />
  );
};

const Input = ({
  type = 'text',
  name,
  placeholder,
  disabled = false,
  labelWidth,
  label,
  defaultValue,
  required = false,
  flexDirection = 'column',
  variant = 'bordered',
  size = 'md',
  align = 'left',
  color = 'default',
  unit,
  outside = false,
  iconName,
  clearable = false,
  onClick,
  onChange,
  autoComplete = 'off',
  hasContext = true,
  showCurrencyValue = false,
  ...props
}: InputProps) => {
  const [inputValue, setInputValue] = useState(defaultValue || '');

  const handleClear = useCallback(() => {
    setInputValue('');
    if (onChange) {
      onChange({ target: { name, value: '' } });
    }
  }, [name, onChange]);

  const handleChange = (
    e: ChangeEvent<HTMLInputElement> | { target: { name: string; value: string | number } }
  ) => {
    setInputValue(e.target.value);
    if (onChange) {
      onChange({ target: { name, value: e.target.value } });
    }
  };

  return (
    <>
      {hasContext ? (
        <ContextInput
          type={type}
          name={name}
          placeholder={placeholder}
          disabled={disabled}
          labelWidth={labelWidth}
          label={label}
          required={required}
          defaultValue={defaultValue}
          flexDirection={flexDirection}
          variant={variant}
          size={size}
          align={align}
          color={color}
          unit={unit}
          outside={outside}
          iconName={iconName}
          clearable={clearable}
          onClick={onClick}
          autoComplete={autoComplete}
          onChange={onChange}
          showCurrencyValue={showCurrencyValue}
        />
      ) : (
        <div
          className={classNames(
            'text-field',
            {
              'text-field--row': flexDirection === 'row'
            },
            {
              'text-field--disabled': disabled
            },
            VARIANT_MAPS[variant],
            SIZE_MAPS[size],
            ALIGN_MAPS[align],
            COLOR_MAPS[color]
          )}
        >
          {label && <Label text={label} required={required} width={labelWidth} color={color} />}
          <div className="text-field__outside">
            <div className="text-field__actions">
              {iconName && (
                <button type="button" className="text-field__icon-pressable" onClick={onClick}>
                  <Icon name={iconName} className="text-field__icon" />
                </button>
              )}
              <input
                name={name}
                className="text-field__form"
                type={type}
                placeholder={placeholder}
                value={inputValue || ''}
                onChange={handleChange}
                disabled={disabled}
                autoComplete={autoComplete}
                {...props}
              />
              {!outside && unit && (
                <Typography size="xs" className="text-field__unit">
                  {unit}
                </Typography>
              )}
              {clearable && !disabled && inputValue !== '' && (
                <button type="button" className="text-field__clearable" onClick={handleClear}>
                  <Icon
                    name="icon-basic-close"
                    size="text-[1.25rem]"
                    color="text-gray-600 hover:text-gray-700"
                  />
                </button>
              )}
            </div>
            {!!outside && unit && (
              <Typography size="xs" className="text-field__outside-unit">
                {unit}
              </Typography>
            )}
          </div>
        </div>
      )}
    </>
  );
};

const MemoizedInput = memo(Input);
export default MemoizedInput;
